import { tableData } from './json'
import { hasClass, removeClass, addClass, siblings } from '../../frame_template/js/utils';
import vToggleProject from './ToggleProject'

export default {
	name: '',

	components: {
		vToggleProject,
	},

	data() {
		return {
			yearList: [],
			year: "",
			monthList: [{
        value: '01',
        label: '1'
      }, {
        value: '02',
        label: '2'
      }, {
        value: '03',
        label: '3'
      }, {
        value: '04',
        label: '4'
      }, {
        value: '05',
        label: '5'
      }, {
        value: '06',
        label: '6'
      }, {
        value: '07',
        label: '7'
      }, {
        value: '08',
        label: '8'
      }, {
        value: '09',
        label: '9'
      }, {
        value: '10',
        label: '10'
      }, {
        value: '11',
        label: '11'
      }, {
        value: '12',
        label: '12'
      }],
			month: "",
			stageList: [],
			stage: "",
			
			radioState: 0,
			tableData: [],
			countData:[],
			ifCheck:sessionStorage.getItem("ifCheck"),
			lastStatus:sessionStorage.getItem("lastStatus"),
			nextStatus:sessionStorage.getItem("nextStatus"),
			status:sessionStorage.getItem("status"),

			//状态码
			CITY: 1,
			DISTRICT: 2,
			SCHOOL: 3,

			showSchool: true,
			showSchoolInfo: true,

			//模态框
			toggleProject: false,
			projectData:[],
			projectId:'',
			projectName:'',
			checked:false,
			ifcheck:'0'
		}
	},

	computed: {
		tMenu() {
			return this.tableData[0].scheduleList;
		}
	},

	created() {
	},

	mounted: function() {
		this.$nextTick(function() {			
			console.log(sessionStorage.getItem("kanbanProjectId"));
			if(sessionStorage.getItem("kanbanProjectId") == 'undefined' || sessionStorage.getItem("kanbanProjectId") == null){
				this.initProject()
			}else{
				this.projectId = sessionStorage.getItem("kanbanProjectId");
				this.projectName = sessionStorage.getItem("kanbanProjectName");
				this.queryYearInit();
				this.queryStage();
			}
			
		})
	},

	methods: {
		changeCheck(){
			if(this.checked){
				this.ifcheck = 'kanban'
			}else{
				this.ifcheck = '0'
			}
			this.queryInit();
		},
		//处理切换显示
		handlerRadioChange(val) {
			console.log(val)
			switch( val ) {
				case 0:
				this.showSchool = true;
				this.showSchoolInfo = true;
				break;
				case 1:
				this.showSchool = false;
				this.showSchoolInfo = true;
				break;
				case 2:
				this.showSchool = false;
				this.showSchoolInfo = false;
			}
		},
		
		initProject(){
			let temp = this;
			this.$post('/web/queryProjectList', {
				"year":"",
				"status":"",
				"projectName":"",
				"pageSize":1000
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.projectData = m.content.page.content;
					temp.projectId = temp.projectData[0].id;
					temp.projectName = temp.projectData[0].name;
					temp.queryYearInit();
					this.queryStage();
					sessionStorage.setItem("kanbanProjectId", temp.projectId);
					sessionStorage.setItem("kanbanProjectName", temp.projectName);
//					console.log(m.content.page);
				}
			})
		},
		
		queryStage(){
			var temp = this;
			this.$post('/web/queryStage', {"projectId":temp.projectId}).then(function(m) {
				temp.stageList=m.content.list;
				temp.stageList.unshift({id: '',name:'请选择'});
			})
		},
		
		queryInit(){
				var temp = this;
				this.$post('/web/kanbanList', {"projectId":temp.projectId,"month":temp.year+"-"+temp.month,"status":temp.lastStatus,"level":"1",stage:temp.stage,"checked" : temp.ifcheck}).then(function(m) {
					temp.tableData=m.content.list;
				})
		},
		
		queryCountInit(){

			var temp = this;
			this.$post('/web/kanbanCount', {"projectId":temp.projectId,"month":temp.year+"-"+temp.month,"status":temp.lastStatus,"level":"1",stage:temp.stage}).then(function(m) {
				temp.countData=m.content.list;
			})
		},
		
		queryYearInit(){
			var temp = this;
			this.$post('/web/queryYear', {"projectId":temp.projectId}).then(function(m) {
				temp.yearList=m.content.list;
				temp.year=m.content.year;
				temp.month=m.content.month;
				temp.queryInit();
				temp.queryCountInit();
				
			})
		},
		
		change(){
			this.queryInit();
			this.queryCountInit();
		},
			
		check(status) {
				var temp = this;
				temp.$post('/web/checkStatus', {
					"checkType": 0,
					"status": status,
					"lastStatus": temp.lastStatus,
					"projectId": temp.projectId,
					"month":temp.year+"-"+temp.month
				}).then(function(m) {
					temp.$message({
						type: 'success',
						message: '审核成功!'
					});						
				}).catch((err) => {
					//console.log(err)
				})
			},

		//行点击展开事件
		openSchoolInfo(e, item) {
			const target = e.target;
			if( item.level == this.DISTRICT ) {
				this.getChildren(`.school_${item.id}`);
			}else if( item.level == this.SCHOOL ) {
				this.getChildren(`.school-info_${item.townId}`);
			}
		},

		getChildren(childClassName) {
			const wrap = document.querySelector("#spectaculars-table");
			let child = wrap.querySelectorAll(childClassName);
			console.log(child)
			child.forEach(i => {
				hasClass(i, 'hide') ? removeClass(i, 'hide') : addClass(i, 'hide')
			})
		},

		//学校重点说明合并单元格
		rowMerge(item) {
			if( !!item.invertedList ) {
				return 4
			}else {
				return 1
			}
		},

		//设置行class
		setRowClass(item) {
			if( item.level == this.DISTRICT ) {
				return 'district'
			}else if( item.level == this.SCHOOL ) {
				return `school_${item.id}`
			}

		}
	}
}



