export default {
  data() {
    return {
      form: {},
      yearList: [],
      statusOptions: [{
      	value: '',
        label: '全部'
      },{
      	value: '1',
        label: '未开始'
      },{
      	value: '2',
        label: '进行中'
      },{
      	value: '3',
        label: '已完成'
      }],
      year: '',
      status: '',
      projectName: '',
      tableData: [],
    };
  },

  computed: {
    isShow: {
      get() {
        return this.show;
      },
      set(val) {
        this.$emit("update:show", val);
      }
    }
  },

  props: {
    show: {
      type: Boolean,
      default: false
    },
    data: {}
  },

  mounted() {
    this.$nextTick(() => {
    	this.initYear();//查询项目时间
			this.init();//查询数据
    });
  },

  methods: {
  	init(){
			let temp = this;
			this.$post('/web/queryProjectList', {
				"year":this.year,
				"status":this.status,
				"projectName":this.projectName,
				"size":1000
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.tableData = m.content.page.content;
//					console.log(m.content.page);
				}
			})
		},
		initYear(){
			let temp = this;
			this.$post('/web/queryProjectYears', {
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.yearList = m.content.list;
//					console.log(m.content.list);
				}
			})			
		},
		
		look(id,name){
			this.isShow = false
			this.$parent.projectId = id;
			this.$parent.projectName = name;
			this.$parent.queryYearInit();
			this.$parent.queryStage();
			sessionStorage.setItem("kanbanProjectId", id);
			sessionStorage.setItem("kanbanProjectName", name);
		},
  	
  }
};



